* {
  box-sizing: border-box;
  border: none;
  outline: 0;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  background-color: #121212;
  justify-content: center;
  align-items: center;
  font-family: Courier New, Courier, monospace;
  display: flex;
}

main {
  width: 100%;
  min-width: 25rem;
  background: url("cat.6f2bd36e.jpg") 50% / cover;
}

.screen {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  font-size: 2rem;
  display: flex;
}

.screenText {
  resize: none;
  width: 100%;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background: none;
  border-bottom: 1px solid #000;
  font-size: 2rem;
}

.buttons {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.buttons :hover {
  border: 2px solid #000;
  transition: border .4s;
}

.buttons button {
  width: calc(25% - 2.1rem);
  height: 5rem;
  cursor: pointer;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background: none;
  border: 2px solid #00000012;
  border-radius: 1.5rem;
  margin: .5rem 1rem;
}

.buttons .button0 {
  width: calc(53% - 2rem);
  margin-right: 25%;
}

.buttons .buttonClear {
  margin-right: 53%;
}

@media screen and (min-width: 500px) {
  main {
    width: 30rem;
  }

  .buttons button {
    width: calc(25% - 2rem);
    height: 6rem;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    cursor: pointer;
    background: none;
    border: 2px solid #00000012;
    border-radius: 1.5rem;
    margin: 1rem;
  }
}

